footer {
  display: flex;
  justify-content: space-between;
  width: 900px;
  margin: 0 auto;
  
  .about-column {
    margin: 44px 0;

    ul {
      list-style: none;
      padding: 0;
      li {
        margin-bottom: 12px;
        a {
          text-decoration: none;
          color: $main-c;
          outline: none;
        }
      }

      li:first-child {
        margin-bottom: 20px;
        font-size: 16px;
        line-height: 19px;
        font-weight: bold;
      }

      li:last-child {
        margin-bottom: 0;
      }
    }
  }

  .about-column:last-child {
    margin-right: 100px;
  }
}
